.navbar-nav {
  max-width: 100%;
  height: 100%;
  display: flex;
  margin-top: 0px;
  justify-content: flex-end;
}


.img {
  height: 10px;
  width: 7px;
}

/* <li> */

.nav-item {
  width: calc(60px * 0.8);
  display: flex;
  align-items: center;
  color: #dadce1;
  justify-content: center;
}

/* Icon Button */

.icon-button {
  --button-size: calc(60px * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  background-color: #3b4555;
  border-radius: 50%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}

.icon-button:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.icon-button svg {
  fill: #dadce1;
  width: 20px;
  height: 20px;
}

/* Dropdown Menu */

.dropdown {
  position: absolute;
  top: 58px;
  width: 300px;
  margin-right: 0px;
  transform: translateX(-45%);
  background-color: #2B333c;
  border: 1px solid #474a4d;
  border-radius: 8px;
  padding: 1.5rem;
  overflow: hidden;
  transition: height 800ms ease;
}

.menu {
  width: 100%;
}

.menu-item {
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: 8px;
  transition: background 500ms;
  padding: 0.9rem;
  padding-bottom: 1rem;
}

.menu-item .icon-button {
  margin-right: 0.5rem;
}

.menu-item .icon-button:hover {
  -webkit-filter: none;
          filter: none;
  cursor: pointer;
}

.menu-item:hover {
  background-color: #3b4555;
}

.icon-right {
  margin-left: auto;
}

/* CSSTransition classes  */

.menu-primary-enter {
  position: absolute;
  transform: translateX(-110%);
}

.menu-primary-enter-active {
  transform: translateX(0%);
  transition: all 500ms ease;
}

.menu-primary-exit {
  position: absolute;
}

.menu-primary-exit-active {
  transform: translateX(-110%);
  transition: all 500ms ease;
}

.menu-secondary-enter {
  transform: translateX(110%);
}

.menu-secondary-enter-active {
  transform: translateX(0%);
  transition: all 500ms ease;
}

.menu-secondary-exit-active {
  transform: translateX(110%);
  transition: all 500ms ease;
}
